<script>
import api from '@/api'
import { getToken } from '@/api/auth'
import moment from 'moment'

export default {
  data() {
    return {
      self: this,
      columns: [
          {
            title: 'ID',
            key: 'id',
            align: 'center',
          },
          {
            title: '游戏审核',
            key: 'action',
            align: 'center',
            render: (h, params) => {
              const status = params.row.game_review;
              const color = status == 1 ? 'success' : 'error';
              const text = status == 1 ? '开启' : '关闭';

              return h('Tag', {
                  props: {
                    type: 'dot',
                    color: color
                  }
                }, text);
              }
          },
          {
            title: '应用审核',
            key: 'action',
            align: 'center',
            render: (h, params) => {
              const status = params.row.app_review;
              const color = status == 1 ? 'success' : 'error';
              const text = status == 1 ? '开启' : '关闭';

              return h('Tag', {
                props: {
                  type: 'dot',
                  color: color
                }
              }, text);
            }
          },
          {
              title: '文章审核',
              key: 'action',
              align: 'center',
              render: (h, params) => {
              const status = params.row.article_review;
              const color = status == 1 ? 'success' : 'error';
              const text = status == 1 ? '开启' : '关闭';

              return h('Tag', {
                props: {
                  type: 'dot',
                  color: color
                }
              }, text);
            }
          },
          {
              title: '修改时间',
              key: 'uptime',
              align: 'center',
          },
          {
            title: '操作',
            key: 'action',
            align: 'center',
            render: (h) => {
              return h('div', [
                h('Button', {
                  domProps: { // 修改标签默认内容
                    innerHTML: "编辑"
                  },
                  props: {
                    type: 'primary',
                    size: 'default',
                  },
                  on: {
                    click: () => {
                      this.$router.push({
                        path: '/admin/configEdit'
                      })
                    }
                  }
                }, 'View')
              ]);
            }
          }
      ],
      data: [],
      isLoading: true,
    }
  },
  mounted: function() {
    const token = getToken();

    api.sysConf({"token": token}).then(res => {
      this.isLoading = false;
      if(res.code == 0) {
        this.data.push(res.data);
        this.data[0].uptime = moment(this.data.uptime).format("YYYY-M-DD k:mm");
      }
      console.log(res,this.data)
    }).catch(err => {
      this.isLoading = false;
      this.$Notice({
        desc: err
      })
    })
  }
}
</script>
<template>
  <div>
    <h3><Icon type="logo-android" size=24 />系统配置</h3>
    <Table border :context="self" :columns="columns" :data="data" :loading=isLoading></Table>
  </div>
  
</template>
<style lang="scss" scoped>
  h3 {
    line-height: 40px;
    font-size: 18px;
    margin-bottom: 20px;
    i {
      margin: -2px 4px 0 0;
    }
  }
</style>